.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background-color: #ffffff;
        box-shadow: 0 0 8px rgba(0,0,0,0.05);
        border-radius: 20px;
        width: 638px;
        max-width: calc(100% - 20px);
        position: relative;
        z-index: 3;
        display: inline-block;
        padding: 24px 0 0;
        overflow: hidden;
    }
    &__top{
        padding: 0 2em;
        text-align: center;
    }
    .close-btn{
        width: 20px;
        @include center;
        flex-shrink: 0;
        position: absolute;
        top: 24px;
        right: 20px;
    }
    &__bottom{
        min-height: 100px;
        background: #f6f7f9;
        padding: 10px 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 24px;
    }
    &__product{
        margin-top: 15px;
        padding: 8px 20px;
        background: #f6f7f9;
        text-align: center;
        font-size: 17px;
        line-height: 130%;
        font-weight: 400;
    }
    &__grid{
        display: grid;
        grid-template-columns: 135px calc(100% - 160px);
        gap: 25px;
        padding: 30px 40px 6px;
    }
    &__img{
        img{
            width: 100%;
            display: block;
            border-radius: 10px;
        }
    }
    &__fields{
        @include grid100gap(16px);
    }
    &__wrap{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 8px));
        gap: 16px;
    }
    &__result{
        padding: 0 30px;
        font-size: 16px;
        line-height: 150%;
        color: #383838;
        font-family: $font2;
        &-num{
            font-size: 30px;
            font-weight: 500;
        }
        &-currency{
            font-size: 20px;
        }
    }
    &__hint{
        font-size: 15px;
        line-height: 120%;
        text-align: center;
        margin-bottom: 18px;
        font-family: $font2;
        color: #5b5b5b;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
        &__grid{
            padding: 20px 15px 0;
            grid-template-columns: 100%;
        }
        &__img{
            max-width: 200px;
            margin: 0 auto;
        }
        &__wrap{
            grid-template-columns: 100%;
        }
    }
}
