.page-top{
    padding: 28px 0;
    .breadcrumbs{
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
    }
}

.breadcrumbs {
    color: #0c0927;
    &__item {
        position: relative;
        display: inline;
        font-size: 15px;
        line-height: 130%;
        font-weight: 300;
        letter-spacing: .01em;
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: -.03125em;
                right: 100%;
                margin-right: .25em;
            }
        }
        &:last-child{
            font-weight: 400;
            pointer-events: none;
        }
        &:hover{
            //color: $darkBlue;
        }
    }
}
