@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 40px;
    height: 50px;
    border-radius: 4px;
    text-transform: uppercase;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 14px;
        line-height: 120%;
    }
}

.m-btn-primary{
    background: $gradient;
    color: #ffffff;
    &:hover{
        color: #ffffff;
        opacity: .9;
    }
}

.m-btn-blue{
    background: $gradient2;
    color: #ffffff;
    height: 40px;
    text-transform: uppercase;
    border-radius: 20px;
    font-weight: 700;
    &:hover{
        color: #ffffff;
        opacity: .9;
    }
}
