@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}


body {
    font-family: $font1;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    min-height: $vh;
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.maincontent{
    flex-grow: 1;
}

.container {
    max-width: 1430px;
    width: 100%;
    margin: 0 auto;
    padding: 0 35px;
}


@media screen and (max-width: 992px){
    .container{
        padding: 0 16px;
    }
}


