.tabs{
  display: flex;
}

.tab{
  background-color: #f9fafb;
  box-shadow: 0px 10px 38px 0px rgba(0,0,0,0.05);
  border-radius: 3px;
  height: 54px;
  padding: 0 28px;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 400;
  font-family: $font2;
  color: #c5c6c7;
  transition: all .3s;
  @include lastM(0 12px 0 0);
  span{
    font-size: 15px;
  }
  &:hover{
    background-color: transparent;
    cursor: pointer;
  }
  &._active{
    background-color: #fff;
    color: #646466;
    pointer-events: none;
    box-shadow: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
