.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    font-family: $font2;
    margin-bottom: 5px;
    span{
      color: $primary;
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/arrow.png);
    background-repeat: no-repeat;
    background-size: 10px auto;
    background-position: right 16px center;
    &::-ms-expand {
      display: none; // IE
    }
  }
  select,
  input,
  textarea{
    border-radius: 7px;
    border: 1px solid #c6c6c6;
    padding: 0 30px 0 16px;
    height: 37px;
    width: 100%;
    font-size: 15px;
    line-height: 150%;
    color: #5b5b5b;
    font-family: $font1;
    background-color: transparent;
    &::placeholder {
      color: #929798;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 37px;
    min-height: 26px;
    padding-top: .0625em;
    font-size: 13px;
    line-height: 140%;
    span{
      line-height: inherit;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #c6c6c6;
      width: 26px;
      height: 26px;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._big{
    label{
      padding-top: 0;
      span{
        font-size: 1em;
        line-height: 140%;
      }
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 10px auto;
      }
    }
  }
  label {
    &:before {
      border-radius: 3px;
      background-image: url(../img/check.png);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 70px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    border: 1px solid #c6c6c6;
    background-color: #fff;
    border-radius:0 7px 7px 0;
    height: 100%;
    width: 26px;
  }
  img,
  svg{
    width: 100%;
  }
  input{
    padding-right: 30px;
  }
  button{
    position: absolute;
    right: 8px;
    width: 10px;
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
  .ccalc-plus{
    top: 8px;
  }
  .ccalc-minus{
    bottom: 8px;
  }
}


